/* Forms
  =============== */

// Display for all input fields (except buttons) 
%forms {
  display: block;
  background: transparent;
  border: $border-width $border-style $border-color;
  border-radius: $border-radius;
  padding: .6rem;
  outline: none;
  vertical-align: middle;
  height: $input-height;
}
// Input fields on hover
%forms-hover {
  border: $border-width $border-style darken($border-color, 10%);
}
// Input fields on focus 
%forms-focus {
  border: 1px solid lighten($link-color, 30%);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .1), 0 0 6px lighten($link-color, 40%);
}
// Variable containing all input fields 
#{$input-fields} {
  width: 100%; // Expands the full width of the container
  max-width: 100%;
  @extend %forms;
  &:hover {
    @extend %forms-hover;
  }
  &:focus {
    @extend %forms-focus;
  }
}
// Horizontal forms have no set width on large screen view 
@include breakpoint {
  .horizontal-form {
    #{$input-fields} {
      display: inline-block;
      width: auto;
    }
  }
}
textarea {
  height: auto;
}
// Placeholder Text 
::-webkit-input-placeholder,
select {
  color: $placeholder;
}
// Fix issues with select
select {
  -webkit-appearance: none;
  background-image: url(data:image/png;base64,);
  background-position: right center;
  background-repeat: no-repeat;
  padding-right: 1.5em;
  line-height: 1;
  -moz-appearance: none;
}
[type=range] {
  width: 100%;
}
// Labels
label {
  font-weight: 600;
  max-width: 100%;
  display: block;
  margin: .5rem 0;
}
.split-form {
  [class*="column"] {
    margin-bottom: 0;
  }
}
@include breakpoint {
  .horizontal-form {
    // Horizontal forms are inline-block
    label,
    #{$input-buttons} {
      display: inline-block;
      margin: .5rem 0;
    }
  }
  .split-form {
    // Split forms have right aligned labels and margins on the column
    label {
      text-align: right;
      padding: 0 .5rem;
    }
    [class*="column"] {
      margin-bottom: .5rem;
      padding: 0;
    }
  }
}
// Errors
input,
select,
textarea {
  &.has-error {
    border: 1px solid $error;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .1), 0 0 6px lighten($error, 35%);
  }
  &:hover,
  &:focus {
    &.has-error {
      @extend .has-error;
    }
  }
}
fieldset {
  border-radius: $border-radius;
  padding: .5rem 1rem;
}
legend {
  padding: 0 .5rem;
}
